import React, {useState} from "react";
import {Column, LargeTextField, Line, SmallTextField} from "../Settings_Layout";

export default function EducationSettings() {

    const [fromServer, setD] = useState([]);


    const addField = (e)=>{
        e.preventDefault();
        setD([...fromServer, {title: '', period: '', description: ''}]);
    };

    const changeField = (key, name, value)=>{
        setD([
            ...fromServer.slice(0,key),
            Object.assign({}, fromServer[key], {[name]:value}),
            ...fromServer.slice(key + 1),
        ]);
    };

    const deleteField = key => {
        setD([
            ...fromServer.slice(0,key),
            null,
            ...fromServer.slice(key + 1),
        ]);
    };

    return(
        <div className={"default_block"}>
            <div className={"default_block__header"}>Образование и работа</div>
            <form>
                {
                    fromServer.map((field, i)=>
                        field && <EducationFieldBlock key={i}
                                                      formKey={i}
                                                      hChange = {changeField}
                                                      dField = {deleteField}
                                                      {...field}
                        />
                    )
                }

                <Line>
                    <button className={"default_button add_field__button"} onClick={addField}>+ Добавить новое поле</button>
                </Line>

                <Line>
                    <button className={"default_button cancel_button"}>Отменить</button>
                    <button className={"default_button confirm_button"}>Сохранить изменения</button>
                </Line>
            </form>
        </div>
    )
}

function EducationFieldBlock(props) {

    const onChange = ({target})=> props.hChange(props.formKey, target.name, target.value);
    const onDelete = (e) => {
        e.preventDefault();
        props.dField(props.formKey);
    };

    return (
        <Column>
            <Line>
                <SmallTextField header={"Название"}
                                type="text"
                                value={props.title}
                                name = "title"
                                onChange={onChange}
                />
                <button className={"default_button education__delete_button"} onClick={onDelete}>x</button>
                <SmallTextField header={"Период времени"}
                                type="text"
                                value={props.period}
                                name = "period"
                                onChange={onChange}
                />
            </Line>
            <Line>
                <LargeTextField header={"Описание"}
                                value={props.description}
                                name = "description"
                                onChange={onChange}
                />
            </Line>
        </Column>
    )
}