import React, {useState, useRef} from "react";

import ProfilePage from "./ProfilePage";
import SettingsPage from "./components/settings/SettingsPage"

import {
  Switch,
  Route,
  Link,
} from "react-router-dom";

import "./scss/menu__static.scss";
import "./scss/profile_page.scss"
import "./scss/settings.scss"

import emblem from "./images/emblem.jpg";
import undefined_avatar_pic from "./images/undefined_avatar.png";

function App(){
  const [left_sidebar_isOpened, change_left_sidebar_state] = useState(false)
  const leftSidebarRef = useRef(null)

  const showMenu = ()=>{
    left_sidebar_isOpened ?
        leftSidebarRef.current.classList.remove("show_menu__effect"):
        leftSidebarRef.current.classList.add("show_menu__effect");
    change_left_sidebar_state(!left_sidebar_isOpened)
  };

  return (
      <>
        <div className={"main_window"}>
          <Switch>
            <Route path="/профиль">
              <ProfilePage />
            </Route>

            <Route path="/настройки">
              <SettingsPage />
            </Route>

          </Switch>
        </div>


        <div className={"header_static"}>

          <img className={"logo header__logo"} src={emblem} alt={"logo"}/>

          <div className={"page_name"}> lol </div>

          <div className={"search_container"}>
            <input type="text" placeholder="Search.." className={"search__input"}/>
            <button type="submit" className={"search__button default_img"}/>
          </div>

          <div className={"notifications__container"}>
              <div className={"notifications__item default_img friend_requests__img"}/>
              <div className={"notifications__item default_img chat_messages__img"}/>
              <div className={"notifications__item default_img other_notifications__img"}/>
          </div>

          <div className={"my_profile"}>
            <img src={undefined_avatar_pic} className={"avatar"} alt={"my_avatar"}/>
            <p className={"my_name"}>Sergey Baranenkov</p>
            <p className={"my_status"}>online</p>
            <div className={"default_img acc_icon"}/>
          </div>

        </div>

        <div className="sidebar_left" ref={leftSidebarRef}>
          <button className={"sidebar_left__item"} onClick={showMenu}>
            <i className={"show_menu__button default_img"}/>
            <span>Скрыть меню</span>
          </button>

          <button className={"sidebar_left__item"}>
            <i className={"show_news__button default_img"}/>
            <span>Новости</span>
          </button>

          <button className={"sidebar_left__item"}>
            <i className={"show_messages__button default_img"}/>
            <span>Сообщения</span>
          </button>

          <button className={"sidebar_left__item"}>
            <i className={"show_friends__button default_img"}/>
            <span>Друзья</span>
          </button>

          <button className={"sidebar_left__item"}>
            <i className={"show_communities__button default_img"}/>
            <span>Сообщества</span>
          </button>

          <button className={"sidebar_left__item"}>
            <i className={"show_music__button default_img"}/>
            <span>Музыка</span>
          </button>

          <button className={"sidebar_left__item"}>
            <i className={"show_video__button default_img"}/>
            <span>Видео</span>
          </button>

          <button className={"sidebar_left__item"}>
            <i className={"show_weather__button default_img"}/>
            <span>Погода</span>
          </button>

          <Link
              className="sidebar_left__item"
              role="button"
              to="/настройки"
          >
            <i className={"show_settings__button default_img"}/>
            <span>Настройки</span>
          </Link>

        </div>
        <div className="sidebar_right"/>
      </>
  );
}

export default App;
