import React from "react";


export function Line({children}) {
    return (
        <div className={"default_container default_line"}>
            {children}
        </div>
    )
}

export function Column({children}) {
    return (
        <div className={"default_container default_column"}>
            {children}
        </div>
    )
}

export function SmallTextField({header, type = 'text', value = '', name, onChange}) {
    return (
        <div className={"default_field"}>
            <p className={"default_field__label"}>{header}</p>
            <input className={"default_field__input"} type={type} name={name} value={value} onChange={onChange}/>
        </div>
    )
}

export function SelectField({header, name, options=[], onChange}){
    return (
        <div className={"default_field"}>
            <p className={"default_field__label"}>{header}</p>
            <select className={"default_field__input"} name = {name} onChange={onChange}>
                {options.map((option)=><option>{option}</option>)}
            </select>
        </div>
    )
}

export function LargeTextField({header, value = '', name, onChange}) {
    return (
        <div className={"default_field"}>
            <p className={"default_field__label"}>{header}</p>
            <textarea className={"default_field__input"} name = {name} value={value} onChange={onChange}/>
        </div>
    )
}