import React, {useState} from "react";
import {Line, SelectField} from "../Settings_Layout";

export default function PrivacyPage() {
    const [voiceNotifications, switchVoiceNotificator] = useState(false)

    const switchButton = (e)=>{
        voiceNotifications ?
            e.target.classList.remove("switch-on"):
            e.target.classList.add("switch-on");
        switchVoiceNotificator(!voiceNotifications)
    };

    return (
        <div className={"default_block"}>
            <div className={"default_block__header"}>Настройки приватности</div>
            <form>
                <Line>
                    <SelectField header={"Кто может отправлять сообщения"} options={["Все","Только друзья"]}/>
                    <SelectField header={"Кто может видеть информацию обо мне"} options={["Все","Только друзья"]}/>
                </Line>
                <Line>
                    <SwitcherField onClick={switchButton}/>
                </Line>

                <Line>
                    <button className={"default_button cancel_button"}>Отменить</button>
                    <button className={"default_button confirm_button"}>Сохранить изменения</button>
                </Line>

            </form>
        </div>
    )
}

function SwitcherField({onClick}) {
    return (
        <>
            <p className={"default_switcher__label"}>Проигрывать звук при получении уведомлений</p>
            <div className="switch-btn" onClick={onClick}/>
        </>
    )
}