import React, {useState} from "react";
import {countries} from "../Countries";
import {Line, SelectField, SmallTextField} from "../Settings_Layout"
export default function BasicPage(){

    const [sex, setSex] = useState(true);

    const changeSex = ()=>{setSex(!sex)};
    return(
        <div className={"default_block"}>
            <div className={"default_block__header"}>Основные настройки</div>
            <form>

                <Line>
                    <SmallTextField type="text" header = "Имя"/>
                    <SmallTextField type="text" header = "Фамилия"/>
                </Line>

                <Line>
                    <SelectField header="Пол" onChange={changeSex} options={["Мужской","Женский"]}/>
                    <SelectField header="Статус"
                                 options={[
                                     '',
                                     sex ? "Женат":"Замужем",
                                     sex ? "Не женат":"Не замужем",
                                     `Влюблен${sex ? "" : "а"}`,
                                     "Все сложно",
                                     "В активном поиске",
                                 ]}
                    />
                </Line>

                <Line>
                    <SmallTextField type="date" header = "Дата рождения"/>
                    <SmallTextField type="tel" header = "Телефон"/>
                </Line>

                <Line>
                    <SelectField header="Страна" options={countries}/>
                    <SmallTextField type="text" header = "Город"/>
                </Line>

                <Line>
                    <button className={"default_button cancel_button"}>Отменить</button>
                    <button className={"default_button confirm_button"}>Сохранить изменения</button>
                </Line>
                
            </form>
        </div>
    )
}